<template>
    <div class="user-popularize">
        <div class="com-title-bar">
            <div class="box">
                <div class="back" @click="$routerBack"></div>
                <div class="title">推广中心</div>
                <div class="action">
                    <div class="button"></div>
                </div>
            </div>
        </div>
        <div class="container">
            <div class="user">
                <div class="avatar">
                    <img src="../../../assets/user/avatar.png" v-if="!user.avatar">
                    <img :src="user.avatar" v-if="user.avatar">
                </div>
                <div class="code">
                    <div class="text">邀请码</div>
                    <div class="display">{{ user.promo_code }}</div>
                </div>
                <div class="qrcode" @click="$router.push('/user/popularize/poster')">
                    <img src="../../../assets/user/popularize/qrcode.png">
                </div>
            </div>
            <div class="grade">
                <div class="this item">
                    <img :src="grade.this.icon">
                </div>
                <div class="progress">
                    <img src="../../../assets/user/popularize/progress.png">
                </div>
                <div class="next item">
                    <img :src="grade.next.icon">
                </div>
            </div>
            <div class="tips">
                距离升级下一个等级还差<span>1</span>人
            </div>
        </div>

        <div class="task">
            <div class="popularize">
                <div class="x-title">推广任务</div>
                <div class="box">
                    <div class="item" v-for="(item,index) in task.popularize" :key="index" v-if="index > 0">
                        <div class="icon"><img :src="item.icon" alt=""></div>
                        <div class="info">
                            <div class="title">{{ item.title }}</div>
                            <div class="text">累计推广<span>{{ item.cumulative }}</span>人： 每日观影次数+<span>{{ item.view }}</span>，畅享<span>{{ item.rate }}</span>片源</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="start" @click="$router.push('/user/popularize/poster')">
            <div class="button">立即推广</div>
        </div>

    </div>
</template>

<script>
    export default {
        data() {
            return {
                user: {
                    avatar: null,
                },
                grade: {
                    this: {
                        title: '英勇黄铜',
                        grade: 0,
                        icon: null
                    },
                    next: {
                        title: '不屈白银',
                        grade: 0,
                        icon: null
                    },
                    progress: 0
                },
                task: {}
            }
        },
        activated() {
            this.initUser();
            this.initGrade();
        },
        mounted() {
            this.initTask();
        },
        methods: {
            initUser() {
                this.$axios.get('/api/app/user/store')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.user = result;
                        }
                    })
            },
            initGrade() {
                this.$axios.get('/api/app/user/grade')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.grade = result;
                        }
                    })
            },
            initTask() {
                this.$axios.get('/api/app/grade/task')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.task = result;
                        }
                    })
            }
        }
    }
</script>

